    font-size: 62.5%;
}

@font-face {
    font-family: RobotoCondensedRegular;
    src: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
}

@font-face {
    font-family: RobotoCondensedBold;
    src: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
}

@media only screen and (max-width: 500px) {
    .options {
        grid-template-columns: 1fr 1fr !important;
        justify-items: center;
        grid-row-gap: 20px;
    }

    .options>li:nth-child(3) {
        grid-area: 2 / 1 / 2 / 3
    }

    label[for='location-search'] {
        font-size: 1.5rem;
    }
}

@media only screen and (max-width: 900px) and (min-width: 500px) {
    header {
        grid-template-columns: 1fr;
        grid-auto-rows: 1fr;
    }

    .options {
        justify-items: center;
    }
}

@media only screen and (min-width: 900px) {
    header {
        grid-template-columns: 1fr 2fr;
    }
}

body {
    box-sizing: border-box;
    margin: 0px;
    padding: 0px;
    font-family: RobotoCondensedRegular, system-ui, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
    display: grid;
    grid-template-rows: 1fr 10fr 1fr;
    max-height: 100vh;
}

header {
    height: 100%;
    display: grid;
    align-items: center;
    justify-items: center;
    padding: 0px 20px;
    box-shadow: 2px 0px 10px #b45309;
}

header, footer {
    background-color: #e5e5e5;
}

footer {
    box-shadow: -2px 0px 10px #b45309;
}

h1, h2 {
    font-family: RobotoCondensedBold;
    letter-spacing: 0.3rem;
    white-space: nowrap;
}

h1 {
    font-size: 3.5rem;
}

h2 {
    font-size: 3rem;
}

h3 {
    font-family: RobotoCondensedBold;
    font-size: 2.2rem;
}

h4 {
    font-size: 2rem;
    padding: 0px;
    margin: 0px;
}

ul {
    list-style: none;
    padding: 0px;
    margin: 0px;
}

.title-container {
    display: flex;
    gap: 20px;
    align-items: center;
}

#header-logo, #condition-icon {
    max-height: 50px;
}

.options {
    display: grid;
    grid-template-columns: 1fr 1fr 1.5fr;
    align-items: center;
    font-size: 2rem;
}

#location-search-form {
    display: flex;
    align-items: center;
    gap: 10px;
}

#location-search {
    font-size: 2rem;
    max-width: 150px;
}

#submit-location-search {
    font-family: inherit;
    font-size: 1.8rem;
    padding: 5px 20px;
    background-color: #fffbeb;
    border: 1px solid #fcd34d;
    border-radius: 10px;
}

#submit-location-search:hover {
    cursor: pointer;
    outline: 2px solid #fbbf24;
}

#submit-location-search:focus, #location-search:focus-visible {
    outline: 2px solid #fbbf24;
}

#current-weather:hover, #forecast-weather:hover {
    width: fit-content;

}
#current-weather:hover, #forecast-weather:hover {
    cursor: pointer;
    font-weight: bold;
}

.content {
    display: grid;
    justify-items: center;
    align-content: space-evenly;
}

.location-details {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.weather-info {
    display: grid;
    grid-template-columns: 2fr 1fr 1fr;
    font-size: 2rem;
    align-items: center;
    row-gap: 15px;
    margin: 5px 5px 20px 5px;
    max-width: 250px;
}

.weather-data {
    justify-self: end;
}

.condition, .date {
    grid-column: 1 / span 3;
    justify-self: center;
    font-weight: bold;
}

.weather-icon {
    padding: 15px;
    justify-self: center;
}

#current-temperature, #current-temperature + .units {
    font-size: 2.5rem
}

#error-msg {
    color: red;
    font-size: 1.5rem;
}

#weather-data-header {
    text-align: center;
}

.weather-container {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    justify-items: center;
}`, "",{"version":3,"sources":["webpack://./src/style.css"],"names":[],"mappings":"AAAA;IACI,gBAAgB;AACpB;;AAEA;IACI,mCAAmC;IACnC,4CAA8C;AAClD;;AAEA;IACI,gCAAgC;IAChC,4CAA2C;AAC/C;;AAEA;IACI;QACI,yCAAyC;QACzC,qBAAqB;QACrB,kBAAkB;IACtB;;IAEA;QACI;IACJ;;IAEA;QACI,iBAAiB;IACrB;AACJ;;AAEA;IACI;QACI,0BAA0B;QAC1B,mBAAmB;IACvB;;IAEA;QACI,qBAAqB;IACzB;AACJ;;AAEA;IACI;QACI,8BAA8B;IAClC;AACJ;;AAEA;IACI,sBAAsB;IACtB,WAAW;IACX,YAAY;IACZ,0JAA0J;IAC1J,aAAa;IACb,gCAAgC;IAChC,iBAAiB;AACrB;;AAEA;IACI,YAAY;IACZ,aAAa;IACb,mBAAmB;IACnB,qBAAqB;IACrB,iBAAiB;IACjB,gCAAgC;AACpC;;AAEA;IACI,yBAAyB;AAC7B;;AAEA;IACI,iCAAiC;AACrC;;AAEA;IACI,gCAAgC;IAChC,sBAAsB;IACtB,mBAAmB;AACvB;;AAEA;IACI,iBAAiB;AACrB;;AAEA;IACI,eAAe;AACnB;;AAEA;IACI,gCAAgC;IAChC,iBAAiB;AACrB;;AAEA;IACI,eAAe;IACf,YAAY;IACZ,WAAW;AACf;;AAEA;IACI,gBAAgB;IAChB,YAAY;IACZ,WAAW;AACf;;AAEA;IACI,aAAa;IACb,SAAS;IACT,mBAAmB;AACvB;;AAEA;IACI,gBAAgB;AACpB;;AAEA;IACI,aAAa;IACb,oCAAoC;IACpC,mBAAmB;IACnB,eAAe;AACnB;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,SAAS;AACb;;AAEA;IACI,eAAe;IACf,gBAAgB;AACpB;;AAEA;IACI,oBAAoB;IACpB,iBAAiB;IACjB,iBAAiB;IACjB,yBAAyB;IACzB,yBAAyB;IACzB,mBAAmB;AACvB;;AAEA;IACI,eAAe;IACf,0BAA0B;AAC9B;;AAEA;IACI,0BAA0B;AAC9B;;AAEA;IACI,kBAAkB;;AAEtB;AACA;IACI,eAAe;IACf,iBAAiB;AACrB;;AAEA;IACI,aAAa;IACb,qBAAqB;IACrB,2BAA2B;AAC/B;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,uBAAuB;AAC3B;;AAEA;IACI,aAAa;IACb,kCAAkC;IAClC,eAAe;IACf,mBAAmB;IACnB,aAAa;IACb,wBAAwB;IACxB,gBAAgB;AACpB;;AAEA;IACI,iBAAiB;AACrB;;AAEA;IACI,uBAAuB;IACvB,oBAAoB;IACpB,iBAAiB;AACrB;;AAEA;IACI,aAAa;IACb,oBAAoB;AACxB;;AAEA;IACI;AACJ;;AAEA;IACI,UAAU;IACV,iBAAiB;AACrB;;AAEA;IACI,kBAAkB;AACtB;;AAEA;IACI,WAAW;IACX,aAAa;IACb,2DAA2D;IAC3D,qBAAqB;AACzB","sourcesContent":[":root {\n    font-size: 62.5%;\n}\n\n@font-face {\n    font-family: RobotoCondensedRegular;\n    src: url(./assets/RobotoCondensed-Regular.ttf);\n}\n\n@font-face {\n    font-family: RobotoCondensedBold;\n    src: url(./assets/RobotoCondensed-Bold.ttf);\n}\n\n@media only screen and (max-width: 500px) {\n 